@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300&family=Open+Sans:wght@400;600;700&family=Roboto:wght@400;500;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300&family=Open+Sans:wght@400;600;700&family=Roboto:wght@400;500;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300&family=Open+Sans:wght@400;600;700&family=Roboto:wght@400;500;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300&family=Open+Sans:wght@400;600;700&family=Roboto:wght@400;500;700&display=swap);
*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

#root {
  width: 100%;
  /* height: 100%; */
  background-image: url("/images/background.png");
  background-repeat: no-repeat;
  background-size: cover;
  overflow-x: hidden;
}


.headline {
  display: block;
  margin: 0;
  position: absolute;
  top: 340px;
  right: 216px;
  width: 550px;
}

.main-headline {
  margin: 0;
  font-size: 115px;
  line-height: 120px;
  font-weight: 700;
  font-family: "Roboto";
}

.promo-headline {
  text-transform: uppercase;
  font-size: 50px;
  line-height: 120px;
  color: #fbf934;
  font-weight: 500;
  font-family: "Roboto";
}

.white-text {
  color: #fff;
}

.welcome-page {
  width: 100vw;
  height: 100vh;
  position: relative;
}

.products-display-container {
  width: 100%;
  /* height: 100%; */
  display: grid;
  margin-top: 50px;
  grid-template-columns: repeat(3, 350px);
  grid-template-rows: repeat(2, 450px);
  grid-gap: 100px 100px;
  align-items: center;
  justify-content: center;
}

.product-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 350px;
  height: 450px;
  position: relative;
  cursor: pointer;
}

.product-container:hover {
  -webkit-filter: drop-shadow(-1.294px 4.83px 25px rgba(221, 223, 224, 0.75));
          filter: drop-shadow(-1.294px 4.83px 25px rgba(221, 223, 224, 0.75));
  background-color: #ffffff;
  transition: 0.5s;
}

.product-icons {
  width: 100%;
  align-items: center;
  justify-content: space-between;
  position: absolute;
  top: 0;
  margin-top: 25px;
}

.product-icons > * {
  display: flex;
  align-items: center;
}

.product-favorite {
  margin-left: 20px;
}

.product-favorite-icon {
  -webkit-filter: invert(67%) sepia(70%) saturate(4973%) hue-rotate(309deg)
    brightness(99%) contrast(98%);
          filter: invert(67%) sepia(70%) saturate(4973%) hue-rotate(309deg)
    brightness(99%) contrast(98%);
}

.product-favorite-num {
  padding-left: 5px;
}

.product-message {
  margin-right: 20px;
}

.product-message-icon {
  -webkit-filter: invert(82%) sepia(37%) saturate(636%) hue-rotate(149deg)
    brightness(94%) contrast(96%);
          filter: invert(82%) sepia(37%) saturate(636%) hue-rotate(149deg)
    brightness(94%) contrast(96%);
}

.product-message-num {
  padding-left: 5px;
}

.product-img-container {
  width: 200px;
  height: 200px;
  text-align: center;
}

.product-img {
  width: auto;
  height: 100%;
}

.product-favorite-icon,
.product-message-icon {
  cursor: pointer;
}

.product-favorite-num,
.product-message-num {
  font-size: 14px;
  letter-spacing: 0px;
  line-height: 25px;
  color: #bbbbbb;
  font-weight: 400;
  font-family: "Open Sans";
  text-align: center;
}

.product-name {
  font-size: 18px;
  letter-spacing: 0;
  line-height: 25px;
  font-weight: 400;
  font-family: "Open Sans";
  text-align: center;
  margin: 40px 0 10px 0;
}

.product-price {
  font-size: 25px;
  letter-spacing: 0;
  line-height: 25px;
  font-weight: 600;
  font-family: "Open Sans";
  text-align: center;
}

.product-add-btn {
  width: 200px;
  height: 60px;
  border-radius: 30px;
  -webkit-filter: drop-shadow(-1.294px 4.83px 12.5px rgba(240, 239, 254, 0.75));
          filter: drop-shadow(-1.294px 4.83px 12.5px rgba(240, 239, 254, 0.75));
  background-image: linear-gradient(26deg, #8b83f9 0%, #a065f4 100%);
  border: none;
  outline: none;
  cursor: pointer;
  font-size: 20px;
  line-height: 26px;
  color: #fff;
  font-weight: 600;
  font-family: "Open Sans";
  position: absolute;
  bottom: -30px;
}

.arrivals-page {
  /* width: 100vw; */
  height: 1325px;
  margin: 0 110px;
  position: relative;
  /* border: 1px solid red; */
  background-color: #fff;
  -webkit-filter: drop-shadow(-1.294px 4.83px 25px rgba(237, 235, 253, 0.75));
          filter: drop-shadow(-1.294px 4.83px 25px rgba(237, 235, 253, 0.75));
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.brands-page {
  height: 1000px;
  /*margin: 0 auto;*/
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.brands-info-container {
  margin: 0 150px;
  max-width: 512px;
}

.brands-header {
  font-size: 50px;
  letter-spacing: 1px;
  line-height: 26px;
  color: #333;
  font-weight: 700;
  font-family: "Open Sans";
  text-align: left;
  padding: 50px 0;
}

.brands-info {
  /*width: 513px;
  height: 256px;*/
  font-size: 16px;
  letter-spacing: 0px;
  line-height: 34px;
  color: #333;
  font-weight: 400;
  font-family: "Open Sans";
}

.brands-info-header {
  font-size: 24px;
  padding-bottom: 20px;
  font-weight: 600;
}

.brands-info-text {
  padding-bottom: 50px;
}

.brands-img-container {
  margin: 0 150px;
}

.iphone-img {
  display: block;
}

.showcase-navbar {
  display: flex;
  justify-content: space-around;
  align-items: center;
  width: 1105px;
  height: 66px;
  border-radius: 33px;
  -webkit-filter: drop-shadow(-1.294px 4.83px 25px rgba(153, 153, 153, 0.897));
          filter: drop-shadow(-1.294px 4.83px 25px rgba(153, 153, 153, 0.897));
  background-color: rgba(255, 255, 255, 0);
}

.showcase-navbar-item {
  list-style: none;
  cursor: pointer;
  padding: 20px 30px;
}

.showcase-navbar-item:hover:not(#active) {
  transform: scale(1.5);
  transition: 0.5s;
}

#active {
  color: #8c84f9;
  font-size: 24px;
}

.showcase-page {
  height: 1380px;
  margin: 0 110px;
  position: relative;
  background-color: #fff;
  -webkit-filter: drop-shadow(-1.294px 4.83px 25px rgba(237, 235, 253, 0.75));
          filter: drop-shadow(-1.294px 4.83px 25px rgba(237, 235, 253, 0.75));
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  z-index: 100;
}

.footer-section {
  height: 350px;
  background-color: #fff;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  position: relative;
  bottom: 0;
  left: 0;
}

.footer-section-title {
  display: block;
  font-size: 40px;
  color: #222222;
  font-weight: 500;
  font-family: "Roboto";
  /* margin-top: 50px; */
  cursor: pointer;
}

.footer-section-menu {
  display: flex;
  flex-direction: row;
  list-style: none;
  align-items: center;
  justify-content: center;
  font-size: 18px;
  line-height: 26px;
  color: #404040;
  font-weight: 600;
  font-family: "Open Sans";
  margin: 50px 0;
  padding: 10px 0;
}

.footer-section-menu-item {
  margin: 0 20px;
  cursor: pointer;
}

.footer-section-menu-item:hover,
.footer-section-social-icon:hover {
  transform: scale(1.2);
}

.footer-section-social-icon {
  margin: 0 20px;
  cursor: pointer;
}

.footer-section-copyright {
  position: absolute;
  bottom: 0;
  margin: 20px;
}

